import styled from 'styled-components';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { deleteRecord, getRecord, getRecordComments } from '../../apis/record';
import { getHabit } from '../../apis/habit';
import { getCurrentUserId } from '../../apis/login';

import ProfileImage from './ProfileImage';
import ProfileName from './ProfileName';
import LikesAndComments from './LikesAndComments';
import like_icon from '../../assets/like_icon.svg';
import comment_icon from '../../assets/comment_icon.svg';
import imgExample1 from '../../assets/IMG_EX_1.png';
import Kebab from './Kebab';
import CalculateTimeDifference from '../functions/CalculateTimeDiff';
import ConfirmModal from './ConfirmModal';
import ConfirmModalLayout from './ConfirmModalLayout';

const PostContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    font-size: 15px;
    margin: 5px 0;
`;
const PostProfile = styled.div`
    display: flex;
    width: calc(100% - 38px);
    max-width: 390px;
    align-items: center;
    justify-content: left;
    margin: 19px;
    position: relative;
    & > div {
        margin-left: 18px;
    }
    & > div:nth-child(1) {
        margin-left: 0;
    }
`;
const ProfileDescription = styled.div`
    display:flex;
    flex-direction: column;
    align-items: left;
    margin-left: 12px;
    * {
        margin: 0;
    }
`;
const PostDate = styled.p`
    font-size: 12px;
    font-weight: 700;
    color: #bebebe;
`
const PostBody = styled.div`
    div:nth-child(1) {
        position: relative;
    }
    & > p {
        margin: 12px 0 20px 0;
        padding: 0 20px 0 20px;
    }
`;
const ImgDescription = styled.div`
    border-radius: 15px;
    background: rgba(44, 44, 44, 0.70);
    box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.20);
    position: absolute;
    right: 18px;
    bottom: 12px;
    & > p {
        color: white;
        font-size: 12px;
        font-weight: 300;
        margin: 8px;
    }
`;
const PostBtn = styled.div`
    display: grid;
    grid-template-columns: 1fr 2px 1fr;
    justify-items: center;
    text-align: center;
    align-items: center;
    margin: 0 auto;
    width: calc(100% - 34px);
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
    div {
        height: 55px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        & > * {
            transition: all .1s ease;
            margin: 2px;
        }
    }
    div:hover {
        img {
            filter: invert(1);
        }
        span {
            opacity: 1;
        }
    }
    span {
        line-height: 55px;
        opacity: .5;
    }
`;
const CommentContainer = styled.div`
    max-width: 390px;
    width: 100%;
    display: flex;
    flex-direction: column;
    span {
        padding: 0 3px;
    }
`;
const CommentProfile = styled.div`
    display: flex;
    width: calc(100% - 38px);
    max-width: 390px;
    align-items: center;
    justify-content: left;
    position: relative;
    font-size: 13px;
    margin: 5px 15px;
    margin-left: 18px;
    left: 0;
`;
const MoreComment = styled.div`
    color: rgba(16, 145, 232, 0.59);
    font-size: 13px;
    font-weight: 700;
    margin: 5px 50px 20px 50px;
    cursor: pointer;
    &:hover {
        text-decoration: underline;
    }
`;
const ModalOverlay = styled.div`
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
`;
function Post({recordId, isModal}) {
    const [habit, setHabit] = useState({});
    const [record, setRecord] = useState({});
    const [comments, setComments] = useState([]);
    
    const [currentUser, setCurrentUser] = useState(0);

    const [modalOpen, setModalOpen] = useState(false);
    const deleteHandler = () => {
        deleteRecord(record.recordId).then(response => {
            if(response) {
                console.log('삭제 완료');
                setModalOpen(false);
            }
        });
    };

    useEffect(() => {
        getRecord(recordId).then((response) => {
            setRecord(response);
            getHabit(response.habitId).then((response) => {
                setHabit(response);
            });
        });
        getRecordComments(recordId).then((response) => {
            setComments(response);
        });
        getCurrentUserId().then(response => {
            setCurrentUser(response);
        })
    }, [])

    const navigate = useNavigate();
    const viewHabitDetailHandler = (habitId) => {
        navigate(`/detail/${habitId}`);
    }
    return (
        <PostContainer>
            {!isModal && (
                <PostProfile>
                    <ProfileImage userId={habit.userId}/>
                    <ProfileDescription>
                        <ProfileName userId={habit.userId} userName={habit.username} fontSize={17}></ProfileName>
                        <PostDate>· {CalculateTimeDifference(record.createdAt, 1)}</PostDate>
                    </ProfileDescription>
                    <Kebab kebabElemList={
                        currentUser === habit.userId ?
                            [{name: '습관 살펴보기', onClick: () => viewHabitDetailHandler(habit.habitId)}, 
                            {name: '공개범위 설정'},
                            {name: '수정하기'},
                            {name: '삭제하기', onClick: () => setModalOpen(true)}]
                        :
                            [{name: '습관 살펴보기', onClick: () => viewHabitDetailHandler(habit.habitId)}, 
                            {name: '신고하기'}]}/>
                </PostProfile>
            )}
            <PostBody>
                <div>
                    <img src={imgExample1}/>
                    <ImgDescription><p>{habit.title} · {CalculateTimeDifference(habit.createdAt, 0)}</p></ImgDescription>
                </div>
                <p>{record.content}</p>
                <LikesAndComments likes={record.like} comments={comments.length}/>
                <PostBtn>
                    <div>
                        <img src={like_icon}/>
                        <span>좋아요</span>
                    </div>
                    <svg width="2" height="14" viewBox="0 0 2 14" fill="none">
                        <path d="M1 0L0.999999 14" stroke="#D8D8D8"/>
                    </svg>
                    <div>
                        <img src={comment_icon}/>
                        <span>댓글 쓰기</span>
                    </div>
                </PostBtn>
            </PostBody>
            <CommentContainer>
                {comments.length > 0 && (
                    <CommentProfile>
                        <ProfileImage imgwidth={20}/>
                        <ProfileName userName={comments[0].username} fontSize={13}></ProfileName>
                        <span>{comments[0].content}</span>
                    </CommentProfile>
                )}
                {comments.length > 1 && (
                    <MoreComment>· {comments.length - 1}개 댓글 더보기</MoreComment>
                )}
            </CommentContainer>
            {modalOpen && (
            <ConfirmModal>
                <ModalOverlay onClick={() => setModalOpen(false)}></ModalOverlay>
                <ConfirmModalLayout
                    message={'정말 삭제하시겠습니까?'}
                    caption={'인증 횟수도 함께 삭제됩니다.'}
                    leftBtn={{
                        "color": '#ff0000',
                        "onClick": deleteHandler,
                        "name": '삭제하기'
                    }}
                    rightBtn={{
                        "color": '#2c2c2c',
                        "onClick": () => setModalOpen(false),
                        "name": '아니오'
                    }}
                />
            </ConfirmModal>
        )}
        </PostContainer>
    );
}

export default Post;