import styled from "styled-components";

const FigureTitle = styled.p`
    color: #a0a0a0;
    font-size: 16px;
    text-align: center;
`;
const FigureValue = styled.p`
    color: black;
    font-size: 19px;
    font-weight: 600;
    text-align: center;
`;

const FigureContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: cetner;
    justify-content: center;
    & > p {
        margin: 7px;
    }
`;

function FigureVertical({title, value}) {
    return (
        <FigureContainer>
            <FigureTitle>{title}</FigureTitle>
            <FigureValue>{value}</FigureValue>
        </FigureContainer>
    );
}

export default FigureVertical;