import styled from "styled-components";
import logo from '../../assets/logo.svg';
import long_logo_typo from '../../assets/long_logo_typo.svg';

const BigLogoBox = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    & > * {
        margin: 5px;
    }
`

function BigLogo() {
    return (
        <BigLogoBox>
            <img src={logo}/>
            <img src={long_logo_typo}/>
        </BigLogoBox>
    );
}

export default BigLogo;