import styled from 'styled-components';
import ProfileImage from '../layout/ProfileImage';
import Kebab from '../layout/Kebab';
import { useNavigate } from 'react-router-dom';

const RecCardContainer = styled.div`
    width: calc(350px - 24px);
    height: calc(60px - 24px);
    margin: 4px 0;
    padding: 12px;
    background-color: white;
    border-radius: 14px;
    border: 0.8px solid #B3B3B3;
    display: flex;
    align-items: center;
    justify-content: left;
    & > div:nth-child(1) {
        width: 35px;
        border: 1px solid #b3b3b3;
    }
    & > p {
        padding-left: 15px;
        width: 70%;
        font-size: 15px;
        font-weight: 700;
        cursor: pointer;
    }
    position: relative;
`;

function RecCard({habitTitle, habitId}) {
    const navigate = useNavigate();
    const clickHandler = () => {
        navigate(`/detail/${habitId}`);
    }
    return (
        <RecCardContainer>
            <ProfileImage imgwidth={35} />
            <p onClick={clickHandler}>{habitTitle}</p>
            <Kebab kebabElemList={[{name: '습관 살펴보기'}, {name: '신고하기'}]}></Kebab>
        </RecCardContainer>
    );
}

export default RecCard;