import styled from "styled-components";
import ProfileImage from "../layout/ProfileImage";
import ProfileName from "../layout/ProfileName";
import Caption from "../string/Caption";

const ProfileBody = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    & > * {
        margin: 10px;
    }
    & > span {
        margin: 0;
    }
`;

function MypageProfile({userName, caption}) {
    return (
        <ProfileBody>
            <ProfileImage imgwidth={82}></ProfileImage>
            <ProfileName userName={userName} fontSize={20} fontWeight={600}></ProfileName>
            <Caption>{caption}</Caption>
        </ProfileBody>
    );
}

export default MypageProfile;