import axios from "axios";
import getToken from "../components/functions/getToken";

export const searchKeyword = async (keyword, cursorId) => {
    const token = getToken();
    // 첫 호출시,
    if (cursorId === 0) 
        return await axios.get(`https://inhamonchallenge.store/api/search?keyword=${encodeURIComponent(keyword)}`, {
            headers: { Authorization: `Bearer ${token.accessToken}`, },
        })
        .then((response) => {
            console.log(response.data);
            return response.data;
        })
        .catch((error) => {
            console.log(error);
            return;
        }); 
    return await axios.get(`https://inhamonchallenge.store/api/search?keyword=${keyword}&cursor=${cursorId}`, {
        headers: { Authorization: `Bearer ${token.accessToken}`, },
    })
    .then((response) => {
        return response.data;
    })
    .catch((error) => {
        console.log(error);
        return;
    });
}