import styled from "styled-components";
import { useEffect, useState } from "react";
import InfiniteScroll from "react-infinite-scroll-component";

import SearchBar from "../components/search/SearchBar";
import Category from "../components/layout/Category";
import { searchKeyword } from "../apis/search";
import SearchCard from "../components/search/SearchCard";

const SearchContainer = styled.div`
    width: 100%;
    margin: 0 auto;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    min-height: calc(100vh - 152px);
    padding-bottom: 92px;
`;

const CategoryList = styled.div`
    width: 90%;
    max-width: 390px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-items: center;
    align-items: center;
`;
const Divider = styled.div`
    width: 370px;
    height: 1px;
    background-color: #e4e4e4;
    margin: 50px;
`;

function Search() {
    const [isCategoryShow, setIsCategoryShow] = useState(true);
    const [searchValue, setSearchValue] = useState("");
    const [items, setItems] = useState();

    const fetchMoreItems = () => {
        searchValue && searchKeyword(searchValue, items[items.length - 1].habitId).then(response => {
            setItems([...items, ...response.data]);
        })
    }

    useEffect(() => {
        searchValue && searchKeyword(searchValue, 0).then(response => {
            setItems(response.data)
        });
    }, [searchValue])

    return (
        <SearchContainer>
            <SearchBar isCategoryShow={setIsCategoryShow}
                searchValue={searchValue}
                setSearchValue={setSearchValue}
            ></SearchBar>
            {isCategoryShow ? (
                <>
                <CategoryList>
                    <div>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                    </div>
                    <div>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                    </div>
                    <div>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                    </div>
                    <div>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                        <Category name={'공부'}></Category>
                    </div>
                </CategoryList>
                <Divider></Divider>
                </>
            ) : (
                Array.isArray(items) &&
                searchValue !== "" &&
                <InfiniteScroll
                    dataLength={items.length}
                    next={fetchMoreItems}
                    hasMore={true}
                >
                    {items.map((item, idx) => 
                        <SearchCard key={idx}>{item.title}</SearchCard>
                    )}
                </InfiniteScroll>
            )}
        </SearchContainer>
    );
}

export default Search;