import styled from "styled-components";

const InputNormal = styled.input`
    width: 280px;
    height: 25px;
    padding: 10px;
    background-color: white;
    border-radius: 4px;
    border: 0.3px solid #d0d0d0;
    &::placeholder {
        color: #d0d0d0;
    }
    &:focus {
        outline: none;
        border: 0.3px solid #c9c9c9;
    }
`;

export default InputNormal;