import styled from "styled-components";
import CategoryTag from "../components/layout/CategoryTag";
import { useState } from "react";
import LongBtn from "../components/btn/LongBtn";
import { getCurrentUserId } from "../apis/login";
import { postHabit } from "../apis/habit";
import { useNavigate } from "react-router-dom";
import ConfirmModal from "../components/layout/ConfirmModal";
import ConfirmModalLayout from "../components/layout/ConfirmModalLayout";

const UploadHabitContainer = styled.div`
    min-height: calc(100vh - 172px);
    width: 100%;
    max-width: 390px;
    margin: 0 auto;
    padding-top: 60px;
    padding-bottom: 92px;
    text-align: center;
    & > div:nth-child(1) {
        margin: 34px 20px 10px 20px;
    }
`;

const RoundDiv = styled.div`
    margin: 0 auto;
    width: 310px;
    height: calc(${props => props.$height}px - 50px);
    font-size: ${props => props.$fontSize}px;
    font-weight: 600;
    font-family: Pretendard;
    padding: 25px 20px;
    background-color: white;
    border-radius: 15px;
    border: 0.5px solid #d6d6d6;
`;
const StyledPre = styled.pre`
    text-align: left;
    margin: 15px 0;
    color: #7D7D7D;
    font-size: 13px;
    font-weight: 500;
    & > strong {
        color: black;
    }
`;
const RadioWrapper = styled.div`
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: left;
    span {
        margin: 8px;
    }
`;

const RoundInput = styled.input`
    margin: 0 auto;
    width: 310px;
    height: calc(${props => props.$height}px - 40px);
    font-size: ${props => props.$fontSize}px;
    font-weight: 600;
    font-family: Pretendard;
    padding: 20px;
    background-color: white;
    border-radius: 15px;
    border: 0.5px solid #d6d6d6;
    &:focus {
        border: 1px solid black;
    }
    &::placeholder {
        color: #bfbfbf;
    }
    vertical-align: top;
`;
const TextLength = styled.div`
    margin: 8px auto 23px auto;
    width: 350px;
    color: #c5c5c5;
    font-size: 13px;
    font-weight: 500;
    text-align: right;
    & > span:nth-child(1) {
        color: #7d7d7d;
    }
`;
const MemoTitle = styled.div`
    width: 315px;
    margin: 21px auto 7px auto;
    color: #7D7D7D;
    font-size: 14px;
    font-weight: 600;
    text-align: left;
`;
const RoundTextarea = styled.textarea`
    margin: 0 auto;
    width: 310px;
    height: calc(${props => props.$height}px - 40px);
    font-size: ${props => props.$fontSize}px;
    font-weight: 600;
    font-family: Pretendard;
    padding: 20px;
    background-color: white;
    border-radius: 15px;
    border: 0.5px solid #d6d6d6;
    &:focus {
        border: 1px solid black;
    }
    &::placeholder {
        color: #bfbfbf;
    }
    vertical-align: top;
    resize: none;
`;
const Footer = styled.div`
    & > * {
        margin: 0 auto;
        width: 350px;
    }
    & > pre {
        padding: 90px 0 10px 10px;
    }
`;
const ModalOverlay = styled.div`
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
`;

function UploadHabit() {
    const [title, setTitle] = useState('');
    const [memo, setMemo] = useState('');
    const [cycle, setCycle] = useState('day');

    // 모달 관련,
    const [modalOpen, setModalOpen] = useState(false);

    const navigate = useNavigate();

    const date = new Date();
    const year = date.getFullYear().toString().substr(-2);
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const day = date.getDate().toString().padStart(2, '0');

    const formattedDate = `${year}년 ${month}월 ${day}일`;

    const titleHandler = (e) => {
        setTitle(e.target.value);
    }
    const memoHandler = (e) => {
        setMemo(e.target.value);
    }
    const radioHandler = (e) => {
        setCycle(e.target.value);
    }

    const uploadHandler = () => {
        const formData = [
            title, memo, "STUDY", (cycle === "day") ? "매일" : "매주", ""
        ];
        postHabit(formData).then(response => {
            console.log(response);
            navigate(`/detail/${response.habitId}`);
        })
    }

    return (
        <UploadHabitContainer>
            <CategoryTag name={'카테고리'}></CategoryTag>
            <RoundInput type="text" $height={60} $fontSize={17} 
                value={title}
                onChange={titleHandler}
                placeholder="목표 습관의 이름을 작성해주세요!"/>
            <TextLength>
                <span>{title.length}</span>
                <span> / 30</span>
            </TextLength>
            <RoundDiv $height={95}>
                <RadioWrapper>
                    <label>
                        <input type="radio" name="checkCycle" 
                        value="day" checked={cycle==="day"}
                        onChange={radioHandler}/>
                        매일
                    </label>
                    <label>
                        <input type="radio" name="checkCycle" 
                        value="week" checked={cycle==="week"}
                        onChange={radioHandler}/>
                        매주
                    </label>
                </RadioWrapper>
                <StyledPre> ·  총 <strong>{cycle === "day" ? '30회' : '4회'}</strong>동안 습관을 실천하게 돼요.</StyledPre>
            </RoundDiv>
            <MemoTitle>상세메모</MemoTitle>
            <RoundTextarea type="text" $height={125} $fontSize={12} 
                value={memo}
                onChange={memoHandler}
                placeholder="목표 습관의 구체적인 상세 메모를 작성해주세요."/>
            <Footer>
                <StyledPre><strong>{formattedDate}</strong> 오늘부터,<br/>
                한 달간 습관을 기록하며 나를 가꾸러 가볼까요?</StyledPre>
                <LongBtn $color={'white'} $backgroundColor={'#1091e8'} $borderColor={'#1091e8'} onClick={() => setModalOpen(true)}>완료</LongBtn>
            </Footer>
        {modalOpen && (
            <ConfirmModal>
                <ModalOverlay onClick={() => setModalOpen(false)}></ModalOverlay>
                <ConfirmModalLayout
                    message={'정말 등록하시겠습니까?'}
                    caption={'이후 수정이 불가합니다.'}
                    leftBtn={{
                        "color": '#1091e8',
                        "onClick": uploadHandler,
                        "name": '등록하기'
                    }}
                    rightBtn={{
                        "color": '#2c2c2c',
                        "onClick": () => setModalOpen(false),
                        "name": '수정하기'
                    }}
                />
            </ConfirmModal>
        )}
        </UploadHabitContainer>
    );
};

export default UploadHabit;