import styled from "styled-components";
import { useState } from "react";
import LongBtn from "../components/btn/LongBtn";
import PrivacyBtn from "../components/btn/PrivacyBtn";
import camera_icon from '../assets/camera.svg';
import delete_icon from '../assets/delete_icon.png';

const UploadRecordContainer = styled.div`
    min-height: calc(100vh - 172px);
    width: 100%;
    background-color: white;
    padding-top: 60px;
    padding-bottom: 92px;
    & > div {
        width: 100%;
        max-width: 390px;
        margin: 0 auto;
    }
`;

const Textarea = styled.textarea`
    width: 100%;
    min-height: 520px;
    border: none;
    resize: none;
    font-weight: 500;
    font-family: Pretendard;
    padding: 17px;
    background-color: white;
    &:focus {
        border: none;
        outline: none;
    }
    &::placeholder {
        color: #bfbfbf;
    }
`;

const BtnGroup = styled.div`
    width: 100%;
    max-width: 390px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & > button {
        width: 350px;
    }
    position: fixed;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
    border-top: 1px solid #cbcbcb;
`;

const BtnTopGroup = styled.div`
    width: 350px;
    height: 35px;
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    & > div {
        & > input {
            display: none;
        }
        & > label {
            cursor: pointer;
        }
    }
`;

const PreviewBox = styled.div`
    width: 110px;
    height: 110px;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(calc(-100% - 20px));
    border: 1px solid #cbcbcb;
    ${props => !props.$isPreview && 'display: none;'}
`;
const DelPreviewBtn = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
    cursor: pointer;
`;
const PreviewImg = styled.img`
    max-width: 110px;
    max-height: 110px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`;

function UploadRecord() {
    const [isPreview, setIsPreview] = useState(false);
    const previewFile = () => {
        const preview = document.querySelector('#preview');
        const file = document.querySelector('#file').files[0];
        const reader = new FileReader();
    
        reader.addEventListener("load", function () {
            // 읽은 결과를 img 태그의 src 속성에 설정
            preview.src = reader.result;
        }, false);
    
        if (file) {
            reader.readAsDataURL(file);
        }
        setIsPreview(true);
    }
    const deletePreview = () => {
        document.querySelector('#preview').src = ""; // 미리보기 이미지 초기화
        document.querySelector('#file').value = ""; // 파일 입력 필드 초기화
        setIsPreview(false);
    }
    
    return (
        <UploadRecordContainer>
            <div>
            <Textarea placeholder="인증글을 작성해주세요..."></Textarea>
            <BtnGroup>
                <BtnTopGroup>
                    <div>
                        <input type="file" id="file" accept="image/*" onChange={previewFile}/>
                        <label for="file">
                            <img src={camera_icon}/>
                        </label>
                    </div>
                    <PrivacyBtn></PrivacyBtn>
                </BtnTopGroup>
                <LongBtn $color={'white'} $backgroundColor={'#1091E8'} $borderColor={'#1091E8'}>공유하기</LongBtn>
                <PreviewBox $isPreview={isPreview}>
                    <PreviewImg id="preview" src=""/>
                    <DelPreviewBtn onClick={deletePreview}><img src={delete_icon}/></DelPreviewBtn>
                </PreviewBox>
            </BtnGroup>
            </div>
        </UploadRecordContainer>
    );
}

export default UploadRecord;