import styled from "styled-components";

const TagContainer = styled.div`
    width: 57px;
    height: 17px;
    border-radius: 10px;
    background-color: ${props => props.$primary ? 'white' : '#3ea6ed'};
    color: ${props => props.$primary ? '#1091e8' : 'white'};
    display: flex;
    justify-content: center;
    align-items: center;
    & > span {
        font-size: 12px;
        font-weight: 200;
        font-family: Pretendard;
    }
`;

function CategoryTag({name, complete}) {
    return(
        <TagContainer $primary={complete}>
            <span>{name}</span>
        </TagContainer>
    );
}

export default CategoryTag;