import { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import PrevSearch from "../btn/PrevSearch";

const SearchBarContainer = styled.div`
    position: relative;
    border: 2px solid #1091e8;
    border-radius: 20px;
    margin: 15px 22px 15px 22px;
    overflow: hidden;
`;
const StyledInput = styled.input`
    width: 300px;
    height: 20px;
    border: none;
    padding: 20px;
    font-size: 18px;
    font-weight: 700;
    font-family: Pretendard;
    &:focus {
        outline: none;
    }
    &::placeholder {
        color: #9cd0f4;
    }
`;
const SearchIcon = styled.div`
    position: absolute;
    right: 0;
    top: 30px;
    transform: translate(-50%, -50%);

`;

function SearchBar(props) {
    const [searchValue, setSearchValue] = [props.searchValue, props.setSearchValue];
    const [showPrevs, setShowPrevs] = useState(false);
    const searchBarContainerRef = useRef(null);

    const getSearchValue = (e) => {
        setSearchValue(e.target.value);
    }
    const onFocus = () => {
        props.isCategoryShow(false);
        if (searchValue === "")
            setShowPrevs(true);
    }
    const onBlur = () => {
        if(!showPrevs && searchValue === "")
            props.isCategoryShow(true);
    }
    const clickOutside = e => {
        if (!searchBarContainerRef.current.contains(e.target)) {
            setShowPrevs(false);
        }
    }
    useEffect(() => {
        document.addEventListener('mousedown', clickOutside);
        return () => {
            document.removeEventListener('mousedown', clickOutside);
        };
    });
    useEffect(() => {
        if(searchValue !== '')
            setShowPrevs(false);
    }, [searchValue]);
    return (
        <SearchBarContainer ref={searchBarContainerRef}>
            <StyledInput 
                type="text"
                placeholder="사람들이 올린 습관 검색하기"
                value={searchValue}
                onChange={getSearchValue}
                onFocus={onFocus}
                onBlur={onBlur}
            />
            <SearchIcon>
                <svg width="29" height="29" viewBox="0 0 29 29" fill="none">
                    <path d="M20.4783 20.4783L27 27" stroke="#1091e8" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"/>
                    <path d="M12.3261 22.6522C18.029 22.6522 22.6522 18.029 22.6522 12.3261C22.6522 6.62315 18.029 2 12.3261 2C6.62315 2 2 6.62315 2 12.3261C2 18.029 6.62315 22.6522 12.3261 22.6522Z" stroke="#1091e8" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
            </SearchIcon>
            <PrevSearch keyword={'밥 챙겨먹기'} setKeyword={setSearchValue} show={showPrevs} setShow={setShowPrevs}></PrevSearch>
            <PrevSearch keyword={'운동하기'} setKeyword={setSearchValue} show={showPrevs} setShow={setShowPrevs}></PrevSearch>
            <PrevSearch keyword={'단어 외우기'} setKeyword={setSearchValue} show={showPrevs} setShow={setShowPrevs}></PrevSearch>
        </SearchBarContainer>
    );
}

export default SearchBar;