import styled from "styled-components";

const ListContainer = styled.div`
    position: absolute;
    right: 15px;
    top: 5px;
    width: 175px;
    border-radius: 10px;
    filter: drop-shadow(0 0 10px hsla(0deg, 0%, 0%, 0.38));
    z-index: 90;
    background-color: white;
`;
const ListElem = styled.div`
    height: 45px;
    width: 100%;
    display: flex;
    align-items: center;
    border-top: 1px solid #b7b7b7;
    &:nth-child(1) {
        border: none;
    }
    P {
        color: ${props => props.$del ? 'red' : '#2c2c2c'};
        width: 100%;
        margin: 20px;
        font-size: 17px;
        font-weight: 500;
    }
`

function KebabList({list}) {
    console.log(list);
    return (
        <ListContainer>
            {list.map((elem, idx) => (
                <ListElem key={idx} $del={elem.name === '삭제하기'} onClick={elem.onClick}>
                    <p>{elem.name}</p>
                </ListElem>
            ))}
        </ListContainer>
    );
}

export default KebabList;