import styled, {keyframes} from 'styled-components';
import { useNavigate, useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';

import { getHabit } from '../apis/habit';
import CategoryTag from '../components/layout/CategoryTag';
import Post from '../components/layout/Post';
import PostModal from '../components/record/PostModal';
import SetSuccessDay from '../components/functions/SetSuccessDay';
import CreatedAtToday from '../components/functions/CreatedAtToday';
import DateToString from '../components/functions/DateToString';
import isToday from '../components/functions/isToday';

import record_add from '../assets/record_add.png';
import record_complete from '../assets/record_complete.png';

const modalIn = keyframes`
    0% {
        top: 100%;
    }
    100% {
        top: 30%;
    }
`;

const HabitDetailContainer = styled.div`
    width: 100%;
    min-width: 390px;
    min-height: calc(100vh - 60px);
    background-color: white;
    margin: 0 auto;
    margin-top: 50px;
    & > h2 {
        width: 330px;
        height: 40px;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const HabitContainer = styled.div`
    width: 350px;
    border-radius: 15px;
    background-color: ${props => props.$primary ? '#c9dff9' : '#f4f9ff'};
    margin: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const DescriptionBox = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-size: 13px;
    font-weight: 500;
    color: ${props => props.$primary ? '#3ea6ed' : '#777777'};
    
    & > div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
        & > * {
            margin-right: 15px;
            margin-left: 15px;
        }
    }
    & > p {
        width: calc(100% - 30px);
    }
`;

const ImgBox = styled.div`
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 15px;
    margin: 3px 4px;
    cursor: pointer;
    background-color: ${props => props.$primary ? '#4eadf4' : 'rgba(0,0,0,.4)'};
    position: relative;
`;
const TodayImg = styled.img`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    ${props => !props.$today && 'display: none;'}
`;
const TitleBlock = styled.div`
    display: flex;
    flex-direction: column;
    align-items: left;
    * {
        margin: 0;
        padding: 0;
    }
    & > div {
        height: 20px;
    }
    & > h2 {
        margin-bottom: 5px;
        font-size: 25px;
        color: black;
    }
`;
const CheckContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: 1fr 1fr 1fr;
`;
const CheckDay = styled.p`
    width: calc(100% - 20px);
    text-align: right;
    margin-top: 30px;
    font-family: 'Pretendard';
    font-size: 25px;
    font-weight: 500;
    color: #3ea6ed;
`;

const ModalOverlay = styled.div`
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
`;
const ModalContainer = styled.div`
    position: fixed;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 390px;
    height: 70%;
    background-color: white;
    border-radius: 20px;
    padding-top: 20px;
    animation: ${modalIn} .2s ease-out forwards;
    z-index: 200;
`;
const ModalBody = styled.div`
    overflow-y: auto;
    overflow-x: hidden;
    min-height: calc(100% - 147px);
    scrollbar-width: none;
    &::-webkit-scrollbar {
        display: none;
    }
`;

const ModalHeader = styled.div`
    width: 390px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > span {
        color: #3ea6ed;
        font-size: 13px;
        font-weight: 700;
        padding: 10px;
    }
`

function HabitDetail() {
    const habitId = useParams().habitId;
    // API로 habitID에 해당되는 습관 정보 받아오기
    const [habit, setHabit] = useState({});
    const [successDay, setSuccessDay] = useState([]);
    const [complete, setComplete] = useState(false);
    // 모달 관련,
    const [modalOpen, setModalOpen] = useState(false);
    const [modalRecordId, setModalRecordId] = useState(1);
    useEffect(() => {
        getHabit(habitId).then(response => {
            setHabit(response);
            console.log(response);
            setSuccessDay(SetSuccessDay(response.createdAt, response.images));
            setComplete(CreatedAtToday(response.createdAt));
        });
    }, []);
    // 페이지 시작시 현재 날짜에 해당되는 칸 선택 되게 하기.
    const clickDay = (idx) => {
        if(Object.keys(successDay[idx]).length !== 0) {
            setModalRecordId(idx);
            setModalOpen(true);
        }
    }
    return (
        <HabitDetailContainer>
        <h2>나의 습관 챌린지</h2>
        <HabitContainer $primary={complete}>
            <DescriptionBox $primary={complete}>
                <div>
                    <TitleBlock>
                        <h2>{habit.title}</h2>
                        <CategoryTag name={habit.category}/>
                    </TitleBlock>
                    <span>{`${DateToString(habit.createdAt)} 부터`}</span>
                </div>
                <p>{habit.content}</p>
            </DescriptionBox>
            <CheckContainer>
            {successDay.map((val, idx) => (
                <Day complete={Object.keys(val).length !== 0} onClick={() => clickDay(idx)} key={idx} today={isToday(habit.createdAt, idx)}></Day>
            ))}
            </CheckContainer>
            <CheckDay>{habit.dayCount}일차 성공!</CheckDay>
        </HabitContainer>
        {modalOpen && (
            <PostModal>
                <ModalOverlay onClick={() => setModalOpen(false)}></ModalOverlay>
                <ModalContainer>
                    <ModalHeader>
                        <span>인증완료!</span>
                        <span>{DateToString(successDay[modalRecordId].createdAt)}</span>
                    </ModalHeader>
                    <ModalBody>
                        <Post recordId={successDay[modalRecordId].recordId} isModal={true}></Post>
                    </ModalBody>
                </ModalContainer>
            </PostModal>
        )}
        </HabitDetailContainer>
    );
}

function Day({complete, onClick, today}) {
    const navigate = useNavigate();
    const handleClickAdd = () => {
        navigate('/uploadrecord');
    }
    return <ImgBox $primary={complete} onClick={(today && !complete) ? handleClickAdd : onClick}>
        <TodayImg src={complete ? record_complete : record_add} $today={today}></TodayImg>
    </ImgBox>;
}

export default HabitDetail;