import axios from "axios";
import getToken from "../components/functions/getToken";

export const postLogin = async (id, pwd) => {
    return await axios.post(`https://inhamonchallenge.store/api/auth/login`, {
        "email": id,
        "password": pwd,
    })
    .then((response) => {
        localStorage.setItem('accessToken', response.data.accessToken);
        localStorage.setItem('refreshToken', response.data.refreshToken);
        localStorage.setItem('accessTokenExpiresIn', response.data.accessTokenExpiresIn);
        return true;
    })
    .catch((error) => {
        console.log(error);
        return false;
    });
}

export const reissueToken = async () => {
    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");
    return await axios.post(`https://inhamonchallenge.store/api/auth/reissue`, {
        "accessToken": accessToken,
        "refreshToken": refreshToken,
    })
    .then((response) => {
        localStorage.setItem('accessToken', response.data.accessToken);
        localStorage.setItem('refreshToken', response.data.refreshToken);
        localStorage.setItem('accessTokenExpiresIn', response.data.accessTokenExpiresIn);
        return true;
    })
    .catch((error) => {
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        localStorage.removeItem('accessTokenExpiresIn');
        console.log("잘못된 리프레시 토큰입니다.");
        return false;
    });
}

export const getCurrentUserId = async () => {
    const token = getToken();
    return await axios.get(`https://inhamonchallenge.store/api/users/current`,
        {
            headers: {Authorization: `Bearer ${token.accessToken}`},
        }
    ).then(response => {
        return response.data.userId;
    }).catch(error => {
        console.log(error);
        return null;
    });
}