import styled from "styled-components";
import { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";

import { getHabits } from "../apis/habit";
import CreatedAtToday from "../components/functions/CreatedAtToday";
import DateToString from "../components/functions/DateToString";
import exImg from '../assets/IMG_EX_1.png';
import H3 from "../components/string/H3";
import ShortBtn from "../components/btn/ShortBtn";
import FollowWrap from "../components/mypage/FollowWrap";
import HabitCard from '../components/layout/HabitCard';
import MypageProfile from "../components/mypage/MypageProfile";
import { getCurrentUserId } from "../apis/login";
import { getFollower, getFollowing, getUserInfo } from "../apis/user";
import getRecordNum from "../components/functions/getRecordNum";

const MypageContainer = styled.div`
    min-height: calc(100vh - 162px);
    width: 100%;
    min-width: 390px;
    margin: 0 auto;
    background-color: white;
    padding-top: 50px;
    padding-bottom: 92px;
`;

const MypageBody = styled.div`
    margin: 0 auto;
    padding-bottom: 22px;
    width: 100%;
    max-width: 390px;
    display: flex;
    flex-direction: column;
    align-items: center;
    button {
        margin-bottom: 30px;
    }
`;
const HabitListBody = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px;
    & > * {
        margin: 10px;
    }
`;

function Mypage() {
    const targetUserId = useParams().userId;
    const [myProfile, setMyProfile] = useState(false);
    // myProfile: 내 마이페이지인지. 아니라면 타인의 마이페이지.
    const [habits, setHabits] = useState([]);
    const [userInfo, setUserInfo] = useState();
    const [following, setFollowing] = useState();
    const [follower, setFollower] = useState();
    useEffect(() => {
        if (targetUserId === '0') {
            setMyProfile(true);
            getCurrentUserId().then(userId => {
                getHabits(userId).then((response) => {
                    console.log(response);
                    setHabits(response);
                });
                getUserInfo(userId).then(response => {
                    setUserInfo(response);
                });
                getFollower(userId).then(response => {
                    setFollower(response);
                })
                getFollowing(userId).then(response => {
                    setFollowing(response);
                })
            });
        } else {
            getHabits(targetUserId).then((response) => {
                console.log(response);
                setHabits(response);
            });
            getUserInfo(targetUserId).then(response => {
                if(response.userId === targetUserId) {
                    setMyProfile(true);
                }
                setUserInfo(response);
            });
            getFollower(targetUserId).then(response => {
                setFollower(response);
            })
            getFollowing(targetUserId).then(response => {
                setFollowing(response);
            })
        }
    }, []);
    return (
        <MypageContainer>
            <MypageBody>
                {myProfile ? (
                    <H3>마이 페이지</H3>
                ) : (
                    <H3></H3>
                )}
                <MypageProfile userName={userInfo && userInfo.username}
                    caption={'습관을 고쳐보려고 노력하는 사람입니다!'}/>
                <Link to="/modify"><ShortBtn color={'#1091e8'} backgroundColor={'white'} borderColor={'#1091e8'} text={'프로필 편집'}></ShortBtn></Link>
                <FollowWrap followingNum={following && following.length} 
                    followerNum={follower && follower.length}
                    recordNum={habits && getRecordNum(habits)}/>
            </MypageBody>
            <HabitListBody>
                <H3>진행하는 습관</H3>
                {habits && 
                habits.map((habit, idx) => (
                <HabitCard
                    habitId={habit.habitId}
                    complete={CreatedAtToday(habit.images)}// complete: 인증 완료 여부
                    imgSrc={exImg}// imgSrc: 이미지 경로
                    completeDate={`${DateToString(habit.createdAt)} 부터`}// completeDate: 인증 날짜
                    category={habit.category}// category: 습관 카테고리
                    title={habit.title}// title: 습관 제목
                    nDay={habit.dayCount}// nDay: 인증 일차
                    key={idx}
                ></HabitCard>
                ))}
            </HabitListBody>
        </MypageContainer>
    );
}

export default Mypage;