import styled from 'styled-components';
import CategoryTag from './CategoryTag';
import { Link } from 'react-router-dom';

const HabitCardContainer = styled.div`
    width: 350px;
    height: 125px;
    border-radius: 15px;
    background-color: ${props => props.$primary ? '#1091e8' : '#f4f9ff'};
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    margin: 5px;
    justify-items: space-between;
    & > div {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin: 12px;
        & > * {
            margin: 3px;
        }
        & > span {
            font-weight: ${props => props.$primary ? '300' : '500'};
            font-size: 13px;
            color: ${props => props.$primary ? '#a4daff' : '#7d7d7d'};
        }
    }
    & > div:nth-child(1) {
        text-align: left;
        & > h2 {
            font-size: 23px;
            & > a {
                font-weight: 600;
            }
            color: ${props => props.$primary ? 'white' : '#777'};
        }
    }
    & > div:nth-child(2) {
        text-align: right;
        & > h2 {
            font-size: 25px;
            font-weight: 300;
            color: ${props => props.$primary ? 'white' : '#777777'};
        }
    }
    h2 {
        margin: 0 0 0 3px;
    }
`;

const DescriptionBox = styled.div`
    height: 120px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-size: 13px;
    font-weight: 700;
    
    & > div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 10px;
        margin-bottom: 10px;
        
        & > span {
            color: ${props => props.$primary ? '#a4daff' : '#777777'};
        }
        & > * {
            margin-right: 15px;
        }
    }
`;

const ImgBox = styled.div`
    width: 38px;
    height: 38px;
    overflow: hidden;
    border-radius: 15px;
    margin-top: 10px;
    margin-left: 7px;
    img {
        height: 40px;
    }
    ${props => props.$primary ? '' : 'border: 1px dashed #827d7d'}
`;
const BigFontBlock = styled.div`
    * {
        margin: 0;
        padding: 0;
    }
    div:nth-child(1) > h2 {
        &:hover {
            text-decoration: underline;
        }
    }
    div:nth-child(2) > h2 {
    }
`;

// complete: 인증 완료 여부
// imgSrc: 이미지 경로
// completeDate: 인증 날짜
// category: 습관 카테고리
// title: 습관 제목
// nDay: 인증 일차
function HabitCard(props) {
    return (
        <HabitCardContainer $primary={props.complete}>
            <div>
                
                <h2><Link to={`/detail/${props.habitId}`} style={{ textDecoration: 'none', color: 'inherit' }}>{props.title}</Link></h2>
                <CategoryTag name={props.category} complete={props.complete}/>
                {/* <DescriptionBox $primary={props.complete}>
                    <BigFontBlock $primary={props.complete}>
                    </BigFontBlock>
                </DescriptionBox> */}
                <ImgBox $primary={props.complete}>{props.complete && <img src={props.imgSrc}></img>}</ImgBox>
            </div>
            <div>
                <span>{props.completeDate}</span>
                <h2>{props.nDay}일차</h2>
            </div>
        </HabitCardContainer>
    );
}

export default HabitCard;