import axios from "axios";
import getToken from "../components/functions/getToken";

export const getRecordComments = async(recordId) => {
    return await axios.get(`https://inhamonchallenge.store/api/comments?feedType=RECORD&feedId=${recordId}`)
    .then((response) => {
        return response.data.data;
    })
    .catch((error) => {
        console.log(error);
        return;
    });
}

export const getRecord = async (recordId) => {
    return await axios.get(`https://inhamonchallenge.store/api/records/${recordId}`)
    .then((response) => {
        return response.data;
    })
    .catch((error) => {
        console.log(error);
        return;
    });
}

export const deleteRecord = async (recordId) => {
    const token = getToken();
    return await axios.delete(`https://inhamonchallenge.store/api/records/${recordId}`, {
        headers: { Authorization: `Bearer ${token.accessToken}`}
    })
    .then(() => {
        return true;
    })
    .catch(error => {
        console.log(error);
        return error;
    });
}