import styled from "styled-components";
import { useState, useEffect, useRef } from "react";
import kebab_icon from '../../assets/hamburger_icon.svg';
import KebabList from "../btn/KebabList";

const KebabBtn = styled.div`
    position: absolute;
    right: 0;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
`;

function Kebab ({kebabElemList}) {
    const [showList, setShowList] = useState(false);
    const ref = useRef(null);
    const clickKebab = () => {
        setShowList(!showList)
    }
    const clickOutside = (e) => {
        if (ref.current && !ref.current.contains(e.target)){
            setShowList(false);
        }
    }
    useEffect(() => {
        // 클릭 이벤트 리스너 추가
        document.addEventListener('mousedown', clickOutside);
    
        return () => {
          // 컴포넌트가 언마운트 될 때 클릭 이벤트 리스너 제거
          document.removeEventListener('mousedown', clickOutside);
        };
    });
    return(
        <KebabBtn onClick={clickKebab}>
            <img src={kebab_icon}/>
            {showList &&
                <div ref={ref}>
                    <KebabList list={kebabElemList}></KebabList>
                </div>
            }
        </KebabBtn>
    );
}

export default Kebab;