import styled from "styled-components";
import { useRef, useState } from "react";
import { getCurrentUserId, postLogin } from "../apis/login";
import { useNavigate } from "react-router-dom";
import BigLogo from "../components/logo/BigLogo";
import InputNormal from "../components/input/InputNormal";
import LongBtn from "../components/btn/LongBtn";

const LoginContainer = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;
const BtnContainer = styled.div`
    display: flex;
    flex-direction: column;
    text-align: center;
    & > * {
        margin: 5px;
    }
`;
const InputContainer = styled(BtnContainer)`
    display: flex;
    flex-direction: column;
    & > div {
        margin: 0;
        margin-right: 7px;
        text-align: right;
    }
    margin: 40px 0;
`;
const WarningMsg = styled.span`
    width: 100%;
    text-align: left;
    margin-left: 7px;
    color: red;
    font-size: 11px;
    opacity: ${props => props.$showMsg ? '1' : '0'};
`;
const FindBox = styled.div`
    span {
        color: #646464;
        font-size: 11px;
        cursor: pointer;
        &:hover {
            text-decoration: underline;
        }
    }
    svg {
        margin-right: 5px;
        margin-left: 5px;
    }
`;

function Login() {
    const [id, setId] = useState('');
    const [pwd, setPwd] = useState('');
    const [showMsg, setShowMsg] = useState(false);
    const ref = useRef();
    const navigate = useNavigate();

    const onChangeHandler = (e) => {
        const {name, value} = e.target;
        if (name === "id") {
            setId(value);
        }
        else {
            setPwd(value);
        }
    };
    const onKeyDownHandler = (e) => {
        if(e.key === 'Enter') {
            onLogin();
        }
    }
    const onLogin = () => {
        ref.current.style.color = 'rgba(255, 255, 255, .6)';
        console.log(id, pwd);
        postLogin(id, pwd).then((response) => {
            if(response) {
                // 로그인 성공
                getCurrentUserId().then(res => {
                    localStorage.setItem("currentUserId", res);
                })
                navigate('/');
            }
            else {
                setShowMsg(true);
            }
        });
        ref.current.style.color = 'rgba(255, 255, 255, 1)';
    }

    return (
        <LoginContainer>
            <BigLogo/>
            <InputContainer>
                <WarningMsg $showMsg={showMsg}>아이디 혹은 비밀번호가 일치하지 않습니다!</WarningMsg>
                <InputNormal 
                    type="text" 
                    placeholder="아이디를 입력해주세요"
                    onChange={onChangeHandler}
                    onKeyDown={onKeyDownHandler}
                    value={id}
                    name="id"
                />
                <InputNormal 
                    type="password" 
                    placeholder="비밀번호를 입력해주세요"
                    onChange={onChangeHandler}
                    onKeyDown={onKeyDownHandler}
                    value={pwd}
                    name="pwd"
                />
                <FindBox>
                    <span>아이디 찾기</span>
                    <svg width="2" height="8" viewBox="0 0 2 8" fill="none">
                        <path d="M1 0L1 8" stroke="#B7B7B7" strokeWidth="0.7"/>
                    </svg>
                    <span>비밀번호 찾기</span>
                </FindBox>
            </InputContainer>
            <BtnContainer>
                <LongBtn 
                    $color={'white'} 
                    $backgroundColor={'#1091e8'} 
                    $borderColor={'#1091e8'}
                    ref={ref}
                    onClick={onLogin}
                >로그인</LongBtn>
                <LongBtn 
                    $color={'#1091e8'} 
                    $backgroundColor={'white'} 
                    $borderColor={'#1091e8'}
                >학교 인증하고 회원가입 하기</LongBtn>
            </BtnContainer>
        </LoginContainer>
    );
}
export default Login;