import styled from "styled-components";
import FigureVertical from "../string/FigureVertical";

const WrapContainer = styled.div`
    width: 100%;
    max-width: 390px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    & > svg {
        margin: 0 15px;
    }
`;

function FollowWrap({followingNum, followerNum, recordNum}) {
    return (
        <WrapContainer>
            <FigureVertical title={'팔로우'} value={followingNum} />
            <svg xmlns="http://www.w3.org/2000/svg" width="2" height="10" viewBox="0 0 2 10" fill="none">
                <path d="M1 0V10" stroke="#A0A0A0"/>
            </svg>
            <FigureVertical title={'팔로워'} value={followerNum} />
            <svg xmlns="http://www.w3.org/2000/svg" width="2" height="10" viewBox="0 0 2 10" fill="none">
                <path d="M1 0V10" stroke="#A0A0A0"/>
            </svg>
            <FigureVertical title={'습관인증'} value={recordNum} />
        </WrapContainer>
    );
}

export default FollowWrap;