import styled from "styled-components";

const PrivacyBtnStyle = styled.button`
    width: 111px;
    height: 33px;
    border-radius: 16px;
    border: 0.7px solid #1091E8;
    background-color: #EDF8FF;
    color: #2F5DD1;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
`;

export default function PrivacyBtn() {
    return (
        <PrivacyBtnStyle>공개범위 설정</PrivacyBtnStyle>
    );
}