import axios from "axios";
import getToken from "../components/functions/getToken";
import handleError from "./error";

export const getHabits = async (userId) => {
    const token = getToken();
    return await axios.get(`https://inhamonchallenge.store/api/users/${userId}/habits`, {
        headers: {
            Authorization: `Bearer ${token.accessToken}`,
        },
    })
    .then((response) => {
        console.log(response.data);
        return response.data.data;
    })
    .catch(handleError);
};

export const getHabit = async (habitId) => {
    return await axios.get(`https://inhamonchallenge.store/api/habits/${habitId}`)
    .then((response) => {
        //console.log(response);
        return response.data;
    })
    .catch(handleError);
};

export const getRecommendation = async () => {
    const token = getToken();
    return  await axios.get(`https://inhamonchallenge.store/api/habits/main/recommend`, {
        headers: {Authorization: `Bearer ${token.accessToken}`}
    })
    .then((response) => {
        return response.data.data;
    })
    .catch(handleError);
};

export const postHabit = async(formDataArr) => {
    const token = getToken();
    const params = {
        "title": formDataArr[0],
        "content": formDataArr[1],
        "category": formDataArr[2],
        "frequency": formDataArr[3],
        "hashTag": [],
    };
    return await axios.post(`https://inhamonchallenge.store/api/habits`, params, {
        headers: { Authorization: `Bearer ${token.accessToken}`}
    })
    .then(response => {
        console.log(response);
        return response.data;
    })
    .catch(handleError);
}