import styled from "styled-components";

const ConfirmModalBox = styled.div`
    width: 242px;
    height: 155px;
    border-radius: 18px;
    background: #FFF;
    box-shadow: 0px 0px 60px 8px rgba(0, 0, 0, 0.15);
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;
const MessageBox = styled.div`
    width: 100%;
    height: 100px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & > p {
        margin: 3px;
    }
    & > p:nth-child(1) {
        color: #2c2c2c;
        font-size: 17px;
        font-weight: 500;
    }
    & > p:nth-child(2) {
        color: #959595;
        font-size: 12px;
        font-weight: 500;
    }
`;
const BtnBox = styled.div`
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
`;

const ColoredBtn = styled.div`
    width: 50%;
    color: ${props => props.$color};
    font-size: 17px;
    font-weight: 500;
    text-align: center;
    cursor: pointer;
`;

function ConfirmModalLayout({message, caption, leftBtn, rightBtn}) {
    return (
        <ConfirmModalBox>
            <MessageBox>
                <p>{message}</p>
                <p>{caption}</p>
            </MessageBox>
            <svg width="242" height="2" viewBox="0 0 242 2" fill="none">
                <path d="M0 1H242" stroke="#B7B7B7" strokeWidth="0.4"/>
            </svg>
            <BtnBox>
                <ColoredBtn $color={leftBtn.color} onClick={leftBtn.onClick}>{leftBtn.name}</ColoredBtn>
                <svg width="2" height="21" viewBox="0 0 2 21" fill="none">
                    <path d="M1 0L0.999999 21" stroke="#B7B7B7" strokeWidth="0.7"/>
                </svg>
                <ColoredBtn $color={rightBtn.color} onClick={rightBtn.onClick}>{rightBtn.name}</ColoredBtn>
            </BtnBox>
        </ConfirmModalBox>
    );
}

export default ConfirmModalLayout;