import styled from "styled-components";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";

import { getHabit } from "../../apis/habit";
import ProfileImage from "../layout/ProfileImage";
import ProfileName from "../layout/ProfileName";

const HabitContainer = styled.div`
    background-color: #f2f2f2;
    max-width: 390px;
    width: 100%;
    margin: 0 auto;
    padding: 5px 0;
`;

const NewHabitContainer = styled.div`
    width: 330px;
    height: 280px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 5px auto;
`;

const ProfilePart = styled.div`
    width: 100%;
    height: 45%;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span {
        margin: 5px;
    }
`;

const DescriptionPart = styled.div`
    width: 100%;
    height: 55%;
    background-color: #1091e8;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

const Description = styled.p`
    margin: 0;
    text-align: center;
    height: 60px;
    font-weight: 300;
    font-size: 17px;
    & > span {
        font-weight: 600;
    }
`

const DetailBtn = styled(Link)`
    width: 100px;
    height: 30px;
    background-color: white;
    font-size: 13px;
    font-weight: 500;
    border-radius: 20px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.25);
    text-decoration: none;
    text-align: center;
    line-height: 30px;
`;

function NewHabit({habitId}) {
    const [habitTitle, setHabitTitle] = useState('');
    const [habitUsername, setHabitUsername] = useState('');
    useEffect(() => {
        getHabit(habitId).then(response => {
            setHabitTitle(response.title);
            setHabitUsername(response.username);
        });
    }, []);
    return (
        <HabitContainer>
        <NewHabitContainer>
            <ProfilePart>
                <span></span>
                <ProfileImage imgwidth={54}/>
                <ProfileName userName={habitUsername} fontSize={13} fontWeight={500}/>
            </ProfilePart>
            <DescriptionPart>
                <Description><span>'{habitTitle}'</span>을 새롭게<br/>시작하셨어요!</Description>
                <DetailBtn to={`/detail/${habitId}`}>살펴보기</DetailBtn>
            </DescriptionPart>
        </NewHabitContainer>
        </HabitContainer>
    );
}

export default NewHabit;