import styled from 'styled-components';
import homeIcon from '../../assets/home.svg';
import userIcon from '../../assets/person.svg';
import plusIcon from '../../assets/plus.svg';
import magnifierIcon from '../../assets/magnifier.svg';
import { Link, useLocation } from 'react-router-dom';

import { reissueToken } from '../../apis/login';
import { useEffect, useState } from 'react';

const NavContainer = styled.div`
    width: 100%;
    height: 92px;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: white;
    z-index: 150;
    ${props => props.$primary && 'display: none;'}
    & > div {
        height: 100%;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        max-width: 390px;
    }
`;

const NavContent = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const StyledLink = styled(Link)`
    text-decoration: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    opacity: ${(props) => props.$primary ? 1 : .5};
    transition: all .1s ease;
    color: black;
    font-size: 10px;
    font-weight: ${(props) => props.$primary ? 600 : 500};
    span {
        padding-top: 7px;
    }
    &:hover {
        font-weight: 600;
        opacity: 1;
    }
`;

function Navbar() {
    const location = useLocation().pathname;
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    useEffect(() => {
        reissueToken().then(response => {
            setIsLoggedIn(response);
        })
    }, [localStorage.getItem("accessToken")]);
    return(
        <NavContainer $primary={location === '/login'}>
            <div>
            <NavContent>
                <StyledLink to="/" $primary={location === '/'}>
                    <img src={homeIcon} width="20px"/>
                    <span>홈</span>
                </StyledLink>
            </NavContent>
            <NavContent>
                <StyledLink to={isLoggedIn ? '/view' : '/login'} $primary={location === '/view'}>
                    <img src={plusIcon} width="20px"/>
                    <span>습관 기록</span>
                </StyledLink>
            </NavContent>
            <NavContent>
                <StyledLink to="/search" $primary={location === '/search'}>
                    <img src={magnifierIcon} width="20px"/>
                    <span>습관 검색</span>
                </StyledLink>
            </NavContent>
            <NavContent>
                <StyledLink to={isLoggedIn ? '/mypage/0' : '/login'} $primary={location === '/mypage/0'}>
                    <img src={userIcon} width="20px"/>
                    <span>마이페이지</span>
                </StyledLink>
            </NavContent>
            </div>
        </NavContainer>
    );
}

export default Navbar;