import styled from "styled-components";

const PrevSearchBtn = styled.p`
    font-size: 18px;
    font-weight: 700;
    color: #5d5d5d;
    display: ${props => props.$show ? 'block' : 'none'};
    padding: 20px;
    width: 100%;
    margin: 0;
    cursor: pointer;
    &:hover {
        background-color: rgba(0, 0, 0, .05);
    }
`;

export default function PrevSearch({keyword, setKeyword, show, setShow}) {
    const handleClick = () => {
        setKeyword(keyword);
        setShow(false);
    }
    return (
        <PrevSearchBtn onClick={handleClick} $show={show}>{keyword}</PrevSearchBtn>
    )
}