export default function SetSuccessDay(createDate, records) {
    const stdDate = new Date(createDate);
    const createDay = stdDate.getTime();
    let ret = [
        {}, {}, {}, {}, {}, {}, {},
        {}, {}, {}, {}, {}, {}, {},
        {}, {}, {}, {}, {}, {}, {},
        {}, {}, {}, {}, {}, {}, {}
    ];
    records.map(record => {
        const recordDate = new Date(record.createdAt);
        const recordDay = recordDate.getTime();

        const timeDiff = Math.abs(createDay - recordDay);
        const dayDiff = Math.floor(timeDiff / (1000 * 60 * 60 * 24));

        ret[dayDiff] = record;
    });
    return ret;
};