import styled from 'styled-components';
import Post from '../components/layout/Post';
import Recommendation from '../components/home/Recommendation';
import NewHabit from '../components/home/NewHabit';

const HomeContainer = styled.div`
    min-height: calc(100vh - 152px);
    width: 100%;
    min-width: 390px;
    margin: 0 auto;
    background-color: white;
    padding-top: 60px;
    padding-bottom: 92px;
`;

const BackgroundContainer = styled.div`
    width: 100%;
    max-width: 390px;
    background-color: #f2f2f2;
    padding: 10px 0 0 0;
    margin: 0 auto;
`;

function Home() {
    return(
        <HomeContainer>
            <BackgroundContainer>
                <Post recordId={3}></Post>
            </BackgroundContainer>
            <BackgroundContainer>
                <Recommendation></Recommendation>
            </BackgroundContainer>
            <BackgroundContainer>
                <Post recordId={33}></Post>
            </BackgroundContainer>
            <BackgroundContainer>
                <NewHabit habitId={1}></NewHabit>
            </BackgroundContainer>
        </HomeContainer>
    );
}

export default Home;