import styled from "styled-components";
import logo_typo from '../../assets/logo_typo.svg';
import alert_icon from '../../assets/alert_icon.svg';
import { useLocation } from "react-router-dom";

import back_icon from '../../assets/chevron_back.svg';

const HeaderContainer = styled.div`
    width: 100%;
    height: 60px;
    background-color: white;
    & > div {
        margin: 0 auto;
        width: 100%;
        height: 100%;
        max-width: 390px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        & > * {
            display: flex;
            align-items: center;
            margin: 15px;
        }
        h3 {
            margin: 20px;
            font-size: 20px;
            font-weight: 700;
        }
    }
    position: fixed;
    margin: 0 auto;
    top: 0;
    z-index: 100;
`;

const LogoBox = styled.div`
    img {
        padding-top: 5px;
        padding-left: 20px;
    }
`;
const AlertBtn = styled.div`
    cursor: pointer;
`;

function Header() {
    const location = useLocation();
    if (location.pathname === '/login') {
        return (
            <HeaderContainer>
                <div><h3>로그인</h3></div>
                <div></div>
            </HeaderContainer>
        );
    }
    else if (location.pathname === '/uploadrecord') {
        return (
            <HeaderContainer>
                <div><img src={back_icon}/><h3>새 게시물</h3><div></div></div>
            </HeaderContainer>
        );
    }
    else {
        return(
            <HeaderContainer>
                <div>
                <LogoBox>
                    <svg width="33" height="30" viewBox="0 0 33 30" fill="none">
                        <path d="M33.0053 28.5383C33.0674 29.3293 32.3985 29.9996 31.5528 29.9996H24.4004C23.7984 29.9996 23.311 29.5438 23.311 28.9807V28.2702C23.311 27.7072 22.8237 27.2514 22.2217 27.2514C21.8346 27.2514 21.4715 27.448 21.2804 27.7608C20.9269 28.3372 20.5733 28.9092 20.2149 29.4857C20.0191 29.7985 19.6607 29.9951 19.2737 29.9951H13.9082C13.5211 29.9951 13.1628 29.8029 12.9669 29.4857C12.6086 28.9092 12.255 28.3328 11.9014 27.7563C11.7056 27.4435 11.3472 27.2469 10.9602 27.2469C10.3582 27.2469 9.87085 27.7027 9.87085 28.2657V28.9763C9.87085 29.5393 9.38351 29.9951 8.7815 29.9951H1.26112C0.525331 29.9951 -0.05757 29.4097 0.00454239 28.7215L1.49046 11.0124C1.56691 10.1544 2.32659 9.49756 3.24394 9.49756C7.78292 9.49756 11.9445 11.8481 14.061 15.6017L15.2125 17.6483C15.3989 17.9835 15.7715 18.189 16.1729 18.189H16.8083C17.2288 18.189 17.611 17.9611 17.7926 17.6081L18.7959 15.6374C20.6975 11.8883 24.7396 9.49756 29.1735 9.49756H29.9236C30.8457 9.49756 31.6102 10.1634 31.6771 11.0258L33.0101 28.5339L33.0053 28.5383Z" fill="#1091E8"/>
                        <path d="M13.679 5.57484C14.4721 6.33003 15.2079 7.17013 15.8003 8.08173C16.1396 8.59562 16.689 9.65468 16.689 9.65468C16.689 9.65468 17.0904 8.98439 17.6159 8.16663C18.6718 6.53559 19.8185 4.93582 21.1707 3.46565C22.3938 2.15188 23.7794 0.896195 25.4994 0.0650323C25.8004 -0.0824321 26.1731 0.0292835 26.3164 0.315275C26.4454 0.565517 26.3594 0.869383 26.111 1.01685C24.6872 1.87035 23.5214 3.12157 22.5085 4.44874C21.3952 5.91892 20.4588 7.52762 19.6083 9.17654C19.1831 10.0032 18.7817 10.8389 18.3899 11.6879L17.807 12.9704L17.7688 13.0508L17.7402 13.1045C17.6159 13.3324 17.4105 13.5245 17.205 13.6362C16.7798 13.8507 16.3689 13.8775 15.915 13.6988C15.6904 13.6049 15.4563 13.4262 15.3082 13.2028L15.227 13.0687L15.2031 13.0285L15.1553 12.9481L14.969 12.6353C14.8448 12.4297 14.7301 12.2241 14.6011 12.032C13.6503 10.459 12.5275 9.30613 11.0894 8.36772L13.6599 5.57931H13.679V5.57484Z" fill="#1091E8"/>
                    </svg>
                    <img src={logo_typo} />
                </LogoBox>
                <AlertBtn><img src={alert_icon} /></AlertBtn>
                </div>
            </HeaderContainer>
        );
    }
}

export default Header;