import styled from "styled-components";

const StyledLikesAndComments = styled.p`
    font-size: 12px;
    font-weight: 700;
    color: #bebebe;
`;

function LikesAndComments({likes, comments}) {
    return(
        <StyledLikesAndComments>좋아요 {likes}개 · 댓글 {comments}개</StyledLikesAndComments>
    );
}

export default LikesAndComments;