import axios from "axios";

export const getUserInfo = async (userId) => {
    return await axios.get(`https://inhamonchallenge.store/api/users/${userId}`)
    .then(response => {
        return response.data;
    })
    .catch(error => {
        console.log(error);
        return null;
    });
}

export const getFollower = async (userId) => {
    return await axios.get(`https://inhamonchallenge.store/api/follow/${userId}/follower`)
    .then(response => {
        return response.data.data;
    })
    .catch(error => {
        console.log(error);
        return null;
    });
}

