import styled from 'styled-components';
import ProfileName from '../layout/ProfileName';
import RecCard from './RecCard';

import { useEffect, useState } from 'react';
import { getRecommendation } from '../../apis/habit';

const RecContainer = styled.div`
    margin: 0 auto;
    max-width: 390px;
    width: 100%;
    background-color: #f2f2f2;
`;
const RecHeading = styled.div`
    background: white;
    border-bottom: 1px solid #e4e4e4;
    margin: 10px auto;
    & > div {
        width: 100%;
        max-width: 390px;
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        padding: 13px;
        margin: 0 auto;
    }
`;
const CardContainer = styled.div`
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

function Recommendation() {
    const [recommendList, setRecommendList] = useState([]);
    useEffect(() => {
        getRecommendation().then((response) => {
            setRecommendList(response);
            console.log(response);
        });
    }, []);
    return(
        <RecContainer>
            <RecHeading>
                <div>
                    <ProfileName userName={'우주 최강 먼챌러'}></ProfileName>
                    <span>님을 위한 습관 추천</span>
                </div>
            </RecHeading>
            <CardContainer>
                {recommendList && 
                recommendList.map((recommend, idx) => (
                    <RecCard habitId={recommend.habitId} habitTitle={recommend.title} key={idx}></RecCard>
                ))}
            </CardContainer>
        </RecContainer>
    );
}

export default Recommendation;