import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const AddHabitContainer = styled.div`
    width: 350px;
    height: 120px;
    border-radius: 15px;
    background-color: #eee;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5px;
    & > div {
        cursor: pointer;
        text-align: center;
        & > div {
            padding: 10px;
        }
    }

    font-size: 13px;
    font-weight: 800;
    color: #777;
`;

function AddHabit() {
    const navigate = useNavigate();
    const onClickHandler = () => {
        navigate('/uploadhabit');
    };
    return (
        <AddHabitContainer>
            <div onClick={onClickHandler}>
                <div>
                    <svg width="26" height="26" viewBox="0 0 26 26" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M13 0L13 26" stroke="#898989" strokeWidth="3"/>
                        <path d="M26 13L8.9407e-08 13" stroke="#898989" strokeWidth="3"/>
                    </svg>
                </div>
                <span>습관 추가하기</span>
            </div>
        </AddHabitContainer>
    );
}

export default AddHabit;