import styled from "styled-components";
import H3 from "../components/string/H3";
import MypageProfile from "../components/mypage/MypageProfile";
import LongBtn from "../components/btn/LongBtn";

const ModifyProfileContainer = styled.div`
    min-height: calc(100vh - 162px);
    width: 100%;
    min-width: 390px;
    margin: 0 auto;
    background-color: white;
    padding-top: 50px;
    padding-bottom: 92px;
`;
const ProfileBody = styled.div`
    margin: 0 auto;
    padding-bottom: 22px;
    width: 100%;
    max-width: 390px;
`;
const BtnsWrap = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 60px;
    & > button {
        margin: 3px;
        width: 350px;
    }
`;

const ProfileDetailWrap = styled.div`
    width: 100%;
    max-width: 390px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 85px 305px;
    border-bottom: 1px solid #b7b7b7;
`;
const ProfileDetailFont = styled.p`
    color: #A0A0A0;
    font-family: Pretendard;
    font-size: 17px;
    font-weight: 500;
    margin: 15px;
`;

function ModifyProfile() {
    return (
        <ModifyProfileContainer>
            <ProfileBody>
                <H3>프로필 편집</H3>
                <MypageProfile userName={'우주최강 먼챌러'}
                    caption={'습관을 고쳐보려고 노력하는 사람입니다!'}/>
            </ProfileBody>
            <ProfileDetailWrap>
                <ProfileDetailFont>이름</ProfileDetailFont>
                <ProfileDetailFont>홍길동</ProfileDetailFont>
            </ProfileDetailWrap>
            <ProfileDetailWrap>
                <ProfileDetailFont>닉네임</ProfileDetailFont>
                <ProfileDetailFont>우주최강 먼챌러</ProfileDetailFont>
            </ProfileDetailWrap>
            <ProfileDetailWrap>
                <ProfileDetailFont>소개</ProfileDetailFont>
                <ProfileDetailFont>습관을 고쳐보려고 노력하는 사람 ...</ProfileDetailFont>
            </ProfileDetailWrap>
            <ProfileDetailWrap>
                <ProfileDetailFont>이메일</ProfileDetailFont>
                <ProfileDetailFont>hong@inha.edu</ProfileDetailFont>
            </ProfileDetailWrap>
            <BtnsWrap>
                <LongBtn $color={'#f00'} $backgroundColor={'white'} $borderColor={'#f00'}>계정 삭제하기</LongBtn>
                <LongBtn $color={'white'} $backgroundColor={'#1091e8'} $borderColor={'#1091e8'}>적용하기</LongBtn>
            </BtnsWrap>
        </ModifyProfileContainer>
    );
}

export default ModifyProfile;