import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { createGlobalStyle } from 'styled-components';
import Navbar from './components/layout/Navbar';
import Header from './components/layout/Header';
import Home from './routes/Home';
import ViewHabits from './routes/ViewHabits';
import Search from './routes/Search';
import HabitDetail from './routes/HabitDetail';
import Login from './routes/Login';
import Mypage from './routes/Mypage';
import ModifyProfile from './routes/ModifyProfile';
import UploadHabit from './routes/UploadHabit';
import UploadRecord from './routes/UploadRecord';

const GlobalStyle = createGlobalStyle`
  body {
    margin: 0 auto;
    padding: 0;
    background-color: #f2f2f2;
    * {
      font-family: Pretendard;
    }
  }
`;

function App() {
  return (
    <Router>
      <GlobalStyle/>
      <Header></Header>
      <Routes>
        <Route path="/" element={<Home/>}></Route>
        <Route path="/login" element={<Login/>}></Route>
        <Route path="/view" element={<ViewHabits/>}></Route>
        <Route path="/uploadhabit" element={<UploadHabit/>}></Route>
        <Route path="/uploadrecord" element={<UploadRecord/>}></Route>
        <Route path="/detail/:habitId" element={<HabitDetail/>}></Route>
        <Route path="/search" element={<Search/>}></Route>
        <Route path="/mypage/:userId" element={<Mypage/>}></Route>
        <Route path="/modify" element={<ModifyProfile/>}></Route>
      </Routes>
      <Navbar></Navbar>
    </Router>
  );
}

export default App;
