import { useNavigate } from 'react-router-dom';
import profileIcon from '../../assets/profile_icon.svg';
import styled from 'styled-components';

const ImgContainer = styled.div`
    width: ${props => props.$imgwidth || 32}px;
    height: ${props => props.$imgwidth || 32}px;
    border-radius: 100%;
    overflow: hidden;
    img {
        width: ${props => props.$imgwidth || 32}px;
        background-color: #d9d9d9;
    }
    cursor: pointer;
`;

function ProfileImage({imgwidth, imgSrc, userId}) {
    const navigate = useNavigate();
    const clickImage = () => {
        navigate(`/mypage/${userId}`);
    }
    return (
        <ImgContainer $imgwidth={imgwidth} onClick={clickImage}>
            <img src={imgSrc || profileIcon}/>
        </ImgContainer>
    );
}

export default ProfileImage;